import { memo } from 'react';
import ApexChart from 'react-apexcharts';
import ApexCharts from 'apexcharts';

const InvestmentListItem = ({script, i}) => {

  const chartOptions = {
    chart: { 
      id: `script{i}`, 
      type: 'line',
      animations: { 
        enabled: false, 
        dynamicAnimation: {
          enabled: false
        }
      }, 
      toolbar: { show: false }, 
      zoom: { enabled: false } ,
    },
    colors: ['#006055'],
    markers: {
      size: 0.0001,
      discrete: [{
        seriesIndex: 0,
        dataPointIndex: script.list.length - 1,
        fillColor: '#03a383',
        strokeColor: '#fff',
        size: 8,
        shape: "circle"
      }]
    },
    stroke: {
      curve: 'smooth',
      width: 3,
      colors: ['#549899'],
    },

    tooltip: {
      theme: 'light',
      insersect: false
    },
    xaxis: { 
      type: 'datetime',
      tickAmount: 12, 
      min: script.info.start,
      max: script.info.start + script.info.duration
    },
    yaxis: { 
      type: 'numeric', 
      title: { text: 'Price (USDT)' },
      labels: { formatter: (val) => val.toFixed(2) }
    }
  };

  return (

    <li className="row" key={script.id} >
      <div className="col-8">
        <ApexChart
          type="line"
          //width={500}
          series={[{ name: "Balance USDT", data: script.list }]}
          options={chartOptions}
        />
      </div>
      <div className="col-4">
        <table className="table">
          <tbody>
            <tr>
              <th>status</th>
              <td>{script.info.status}</td>
            </tr>
            <tr>
              <th>start</th>
              <td>{new Date(script.info.start).toLocaleDateString("ru-RU")}</td>
            </tr>
            <tr>
              <th>end</th>
              <td>{new Date(script.info.start + script.info.duration).toLocaleDateString("ru-RU")}</td>
            </tr>
            <tr>
              <th>invested</th>
              <td>{script.info.sum}</td>
            </tr>
          </tbody>
        </table>
      </div>
    </li>
  )
}

export default memo(InvestmentListItem, (prev, next) => JSON.stringify(prev) === JSON.stringify(next));
