import { useEffect, useState } from 'react';

import InvestmentListItem from './InvestmentListItem.js';
import { useGetAllInvestmentsQuery, selectAllInvestments } from '../../features/clients/clientApiSlice.js';

import { useSelector } from 'react-redux';
import { selectClient } from '../../features/clients/clientSlice.js';

const InvestmentList = () => {
  const { refetch } = useGetAllInvestmentsQuery();
  useEffect(() => {
    const i = setInterval(() => {
      refetch()      
    }, 10000);

    return () => clearTimeout(i);
  }, []);

  const scripts = useSelector(state => selectAllInvestments(state))

  return (
    <ul className="ul balance-widget mb-3">
      {scripts && scripts.length !== 0 && scripts
        .sort(({info: ai},{info: bi}) => ai.status === 'inprocess' ? bi.status === 'inprocess' ? bi.start - ai.start : -1 : 1)
        .map((script, i) => 
        <InvestmentListItem script={script} i={i}/>
      )}
    </ul>
  );
}

export default InvestmentList;
