import {
  createSelector,
  createEntityAdapter
} from "@reduxjs/toolkit";
import { apiSlice } from "../../app/api/apiSlice";
import { setClient } from './clientSlice.js';

const investmentsAdapter = createEntityAdapter({})

const initialState = investmentsAdapter.getInitialState()

export const clientApiSlice = apiSlice.injectEndpoints({
  endpoints: builder => ({
    getClientAccountDetails: builder.mutation({
      query: () => ({
        url: '/account',
        method: 'GET',
      }),
      async onQueryStarted(arg, { dispatch, queryFulfilled }) {
        try {
          const { data } = await queryFulfilled
          dispatch(setClient({data}));
        } catch (err) {
          console.log(err)
        }
      }
    }),
    updateClientAccount: builder.mutation({
      query: account => ({
        url: '/account',
        method: 'PATCH',
        body: { ...account }
      }),
      async onQueryStarted(arg, { dispatch, queryFulfilled }) {
        try {
          const { data } = await queryFulfilled;
          dispatch(setClient({ data }));
        } catch (err) {
          console.log(err)
        }
      }
    }),
    getAllInvestments: builder.query({
      query: () => ({
        url: '/investments',
        validateStatus: (response, result) => {
          return response.status === 200 && !result.isError
        },
      }),
      transformResponse: responseData => {
        const loadedInvestments = responseData.map(investment => {
          return investment;
        });
        return investmentsAdapter.setAll(initialState, loadedInvestments)
      },
    })
    //sendLogout: builder.mutation({
    //  query: () => ({
    //    url: '/account/logout',
    //    method: 'POST',
    //  }),
    //  async onQueryStarted(arg, { dispatch, queryFulfilled }) {
    //    try {
    //      const { data } = await queryFulfilled
    //      console.log(data)
    //      dispatch(logOut())
    //      setTimeout(() => {
    //        dispatch(apiSlice.util.resetApiState())
    //      }, 2000)
    //    } catch (err) {
    //      console.log(err)
    //    }
    //  }
    //}),
    //refresh: builder.mutation({
    //  query: () => ({
    //    url: '/account/refresh',
    //    method: 'GET',
    //  }),
    //  async onQueryStarted(arg, { dispatch, queryFulfilled }) {
    //    try {
    //      const { data } = await queryFulfilled
    //      const { accessToken } = data
    //      dispatch(setCredentials({ accessToken }))
    //    } catch (err) {
    //      console.log(err)
    //    }
    //  }
    //}),
  })
});

export const selectInvestmentsResult = clientApiSlice.endpoints.getAllInvestments.select();

// creates memoized selector
const selectInvestmentsData = createSelector(
  selectInvestmentsResult,
  investmentsResult => investmentsResult.data // normalized state object with ids & entities
);

export const {
  selectAll: selectAllInvestments,
  selectById: selectInvestmentById,
  selectIds: selectInvestmentIds
} = investmentsAdapter.getSelectors(state => selectInvestmentsData(state) ?? initialState);

export const {
  useGetClientsQuery,
  useRegisterClientMutation,
  useAddNewClientMutation,
  useUpdateClientMutation,
  useDeleteClientMutation,
} = clientApiSlice;

export const {
  useGetAllInvestmentsQuery,
  useGetClientAccountDetailsMutation,
  useUpdateClientAccountMutation,
  useSendLogoutMutation,
  useRefreshMutation,
} = clientApiSlice ;
